.page__header {
  background-color: $color-graphite-gray;
  color: $color-white;
  background-repeat: no-repeat;
  background-image: url('../img/bg-mobile.jpg');
  background-size: cover;
    @media (min-width: $breakpoint-small) {
      background-image: url('../img/bg-tablet.jpg');
    }
    @media (min-width: $breakpoint-large) {
      background-image: url('../img/bg-desktop.jpg');
    }
}

.header__wrapper {
  @media (min-width: $breakpoint-medium) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto auto auto auto;
  }
}
.page__header--image {
  position: relative;
}

.page-header__nav {
  margin-bottom: 110px;
  background: rgba (0, 0, 0, 0.3);
}

.page-header__bar {
  z-index: 1;
  display: flex;
  justify-content: space-between;
  padding-top: 22px;
  padding-bottom: 21px;
}

.page-header__toggler {
    width: 50px;
    height: 24px;
    background-color: transparent;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    background-image: url(../img/icons/burger.svg);
    border: none;
    @media (max-width: $breakpoint-medium-down) {
      .page-header__nav--closed & {
        display: block;
      }
      .page-header__nav--opened & {
        display: block;
        background-image: url("../img/icons/close.svg");
      }
    }

    @media (min-width: $breakpoint-medium) {
      display: none;
    }
}

.page-header__nav--opened .page-header__menu {
  @media (max-width: $breakpoint-small-down) {
    position: absolute;
    right: 0;
    left: 0;
    z-index: 2;
    display: block;
  }
}

.page-header__nav--closed .page-header__menu {
  @media (max-width: $breakpoint-medium-down) {
    display: none;
  }
}

.header__title {
  margin: 0;
  font-weight: 300;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: $color-white;
  @media (min-width: $breakpoint-medium) {
    font-size: 40px;
    line-height: 48px;
  }
  @media (min-width: $breakpoint-large) {
    font-size: 50px;
    line-height: 68px;
  }
}

.header__title--main {
  margin-bottom: 34px;
  @media (min-width: $breakpoint-medium) {
    grid-column: 1/2;
    grid-row: 1/2;
    margin-bottom: 29px;
    margin-top: 0;
    margin-left: 51px;
    width: 300px;
    font-size: 40px;
    line-height: 48px;
  }
  @media (min-width: $breakpoint-large) {
    margin-bottom: 51px;
    font-size: 50px;
    line-height: 68px
  }
}

.header__title--inner {
  padding-bottom: 51px;
  @media (min-width: $breakpoint-medium)  {
    padding-bottom: 74px;
  }
  @media (min-width: $breakpoint-large)  {
    padding-bottom: 104px;
  }
}

.header__phone {
  grid-column: 1/34;
  grid-row: 1/-1;

}



.header__button {
  margin-bottom: 35px;
  padding: 15px 46px;
  background-color: transparent;
  color: $color-white;
  border: 3px solid $color-white;
  border-radius: 29px 29px 29px 29px;
  font-size: 18px;
  line-height: 18px;
  font-weight: 700;
  @media (min-width: $breakpoint-medium) {
    grid-column: 1/2;
    grid-row: 2/3;

  }
}

.header__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-left: 38px;
  margin-right: 38px;
  margin-bottom: 28px;
  padding: 0;
  min-height: 56px;
  list-style-type: none;
  @media (min-width: $breakpoint-medium) {
    margin-left: 48px;
    margin-right: 48px;
    grid-column: 1/2;
    grid-row: 3/4;

  }
}

.header__item:not(:last-child) {
  margin-right: 35px
}

.header__item--apple {
  width: 47px;
  height: 56px;
  background: url('../img/apple-logo.svg');
  background-repeat: no-repeat;
}

.header__item--android {
  width: 43px;
  height: 47px;
  background: url('../img/android-logo.svg');
  background-repeat: no-repeat;
}

.header__item--microsoft {
  width: 44px;
  height: 41px;
  background: url('../img/microsoft-logo.svg');
  background-repeat: no-repeat;
}

.header__paragraph {
  padding-bottom: 38px;
  font-size: 14px;
  line-height: 24px;
  font-weight: 300;
  @media (min-width: $breakpoint-medium) {
    grid-column: 1/2;
    grid-row: 4/5;
    width: 310px;
    margin-left: 5px;
    padding-bottom: 119px;
    font-size: 18px;
  }
}


